import Vue from 'vue';
import Vuetify from 'vuetify';
import http from 'vue-resource';

import App from './App';
import router from './router';


Vue.use(http);
Vue.use(Vuetify);
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App),
});



