<template>
  <v-container fluid v-html="markdown">

  </v-container>
</template>

<style>
  img {
    max-width: 90%;
    margin-top: 10px;
  }
</style>



<script>
/* eslint-disable */
import marked from 'marked';
import highlightjs from 'highlight.js';
import 'highlight.js/styles/androidstudio.css';

const REPO_BASE_URL = repo =>
  `https://raw.githubusercontent.com/joseahr/${repo}/master/README.md`;

export default {
  components: {
    //VueMarkdown,
  },
  data() {
    return {
      markdown: '',
    };
  },
  methods: {
    getReadme(repo) {
      console.log(repo)
      this.$http.get(REPO_BASE_URL(repo))
        .then(res => res.text())
        .then((markdown) => {
          marked(markdown, { sanitize : true }, (err, mdParsed)=> {
            if(err) return;
            this.markdown = mdParsed;
          });
        })
        .catch(console.log);
    },
    highlight() {
      Array.from(document.querySelectorAll('pre code'))
      .forEach(node => highlightjs.highlightBlock(node))
    },
  },
  created() {
    //console.log(this.$route.params.repoName);
    const { repoName } = this.$route.params;
    this.getReadme(repoName);
  },
  updated() {
    this.highlight();
  }
};
</script>



